import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    darkmode: false
  },
  mutations: {
    TOGGLE_DARKMODE: (state) => {
      state.darkmode = !state.darkmode
    }
  },
  actions: {
    toggleDarkmode(context) {
      context.commit('TOGGLE_DARKMODE')
    }
  },
  getters: {
    darkmode: state => state.darkmode
  }
})
